@use "vars" as *;


.inner {
	display: block;
	padding: 0 vw(15);
	margin: 0 auto;
	@include mq(md-min){
		max-width: vw(1230);
	}
	@include mq(){
		padding: 0 vwsp(50);
	}
}

.btn-img{
	display: block;
	position: relative;
	img{
		width: 100%;
	}
	.img-01{

	}
	.img-02{
		position: absolute;
		top: 0;
		left: 0;
		opacity: 0;
		visibility: hidden;
	}
	&:hover{
		opacity: 1;
		.img-01{
			opacity: 0;
			visibility: visible;
		}
		.img-02{
			opacity: 1;
			visibility: visible;
		}
	}
}



.js-bar,.slimScrollDiv{
	height: vw(436) !important;
	@include mq(){
		height: 66vh !important;
	}
}
.js-bar{
	padding: 0 30px;
	@include mq(){
		padding: 0 vwsp(30);
	}
}
.slimScrollBar{
	display: block !important;
	background: #3a8c54 !important;
	opacity: 1 !important;
}
.slimScrollRail {
	background: #cbd1d3 !important;
	opacity: 1 !important;
	display: block !important;
}

.form-control{
	display: block;
	width: 100%;
	height: 45px;
	background: white;
	border: 1px solid #ccc;
	font-weight: 500;
	padding: 5px 10px;
	@include font-size(20);
	border-radius: 10px;
	&:focus{
		border-color: #66afe9;
		outline: 0;
		-webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(102,175,233,.6);
		box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(102,175,233,.6);
	}
	&::-webkit-input-placeholder {
		color: #6c6c6c;
	}
	&:-moz-placeholder {
		color: #6c6c6c;
	}
	&::-moz-placeholder {
		color: #6c6c6c;
	}
	&:-ms-input-placeholder {
		color: #6c6c6c;
	}
}

.pure-list{
	@include flex(0,0,wrap);
	margin-bottom: 35px;
	@include mq(){
		margin-bottom: 28px;
	}
	li{
		margin-right: 39px;
		@include mq(){
			width: 100%;
			margin: 0 0 2px;
		}
		&:last-child{
			margin-right: 0;
		}
	}
	&__input{
		position: relative;
		input{
			position: absolute;
			top:  0;
			left: 0;
			right: 0;
			bottom: 0;
			z-index: 1;
			opacity: 0;
			cursor: pointer;
			&[type="checkbox"]{
				& + span{
					&:after{
						background-color: transparent;
						top: 23px;
						left: 5px;
						width: 17px;
						height: 10px;
						border-style: solid;
						border-color: #39ecaf;
						border-width: 0 0 4px 4px;
						-o-border-image: none;
						border-image: none;
						-webkit-transform: rotate(-45deg) scale(0);
						-ms-transform: rotate(-45deg) scale(0);
						transform: rotate(-45deg) scale(0);
					}
				}
				&:checked{
					& + span{
						&:after{
							content: '';
							transform: rotate(-45deg) scale(1);
							transition: transform 200ms ease-out;
						}
					}
				}
			}
			&[type="radio"]{
				+ span{
					&:after,&:before{
						border-radius: 50%;
					}
				}
				&:checked{
					& + span{
						&:before{
							animation: borderscale 300ms ease-in;
						}
						&:after{
							transform: scale(1);
						}
					}
				}
			}
		}
		span{
			display: block;
			position: relative;
			padding: 10px 0 10px 40px;
			vertical-align: middle;
			user-select: none;
			cursor: pointer;
			&:before{
				content: "";
				position: absolute;
				top: 17px;
				left: 0;
				width: 31px;
				height: 31px;
				background: #3f6558;
				text-align: center;
				transition: all 0.3s ease;
			}
			&:after{
				content: "";
				background-color: #718CC7;
				position: absolute;
				top: 18px;
				left: 6px;
				width: 14px;
				height: 14px;
				transform: scale(0);
				transform-origin: 50%;
				transition: transform 200ms ease-out, -webkit-transform 200ms ease-out;
			}
		}
	}
}